html{
  background: url('../src/assets/images/body_bg.png');
}
.app-wrapper {
  display: grid;
  max-width: 1200px;
  margin: 0 auto;
  grid-template-areas:
    "header header"
    "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}
.app-wrapper-content {
  grid-area: content;
  background-color: lightskyblue;
}
.imgResponsive {
  width: 100%;
  height: auto;
}
